import { Component, OnInit } from '@angular/core';
import {ValidateService} from "../../../services/validate/validate.service";
import { AuthService} from "../../../services/auth/auth.service";
import { FlashMessagesService} from "angular2-flash-messages";
import {UserService} from "../../../services/user/user.service";
import {Router} from "@angular/router";
// import {NgForm} from "@angular/forms";
// import {User} from "../../objects/user";
// import {forEach} from "@angular/router/src/utils/collection";

@Component({
  selector: 'app-update',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.css']
})
export class UpdateComponent implements OnInit {

  oldUser: any;
  name: String;
  username: String;
  password: String;
  email: String;
  isActive = [];

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router,
    private userService: UserService) { }

  ngOnInit() {
    this.oldUser = this.authService.getUser();
    this.name = this.oldUser.name;
    this.username = this.oldUser.username;
    this.password = "";
    this.email = this.oldUser.email;
    this.isActive = [true, true, true];

  }



  getANewUser(){
    var result = [];

    if(this.isActive[0]){
      result.push(this.name);
    }
    else{
      result.push(this.oldUser.name);
    }
    if(this.isActive[1]){
      result.push(this.username);
    }
    else{
      result.push(this.oldUser.username);
    }
    if(this.isActive[2]){
      result.push(this.email);
    }
    else{
      result.push(this.oldUser.email);
    }
    result.push(this.password);
    return result;
  }

  onUpdateSubmit(){
    const valuesOfUser = this.getANewUser();
    const user = this.oldUser;
    const userToUpdate = {
      name: valuesOfUser[0],
      username: valuesOfUser[1],
      email: valuesOfUser[2],
      password: valuesOfUser[3]
    };
    console.log(userToUpdate);
    // Required fields
    if(!this.validateService.validateForm(userToUpdate)){
      this.flashMessage.show('Please fill all fields', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    //Validate email
    if(!this.validateService.validateEmail(userToUpdate.email)){
      this.flashMessage.show('Please put a correct email', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    //Register user
    this.userService.updateUser(userToUpdate).subscribe(data => {
      if(data.success){
        this.flashMessage.show('You have updated your information \n' +
          'Please log in again', {cssClass: 'alert-success', timeout: 5000});
        this.authService.logout();
        this.router.navigate(['/login']);
      }
      else{
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
      }
    });

  }

}



